h1 {
    background: #333;
    color: #fff;
    text-align: center;
   
    padding: 0.5em;
    
  }
  
  .cardBox {
    float: left;
    font-size: 1.2em;
    margin: 1% 0 0 1%;
    perspective: 800px;
    transition: all 0.3s ease 0s;
    width: 23.7%;
    
  }
  
  .cardBox:hover .card {
    transform: rotateY( 180deg);
  }
  
  .card {
    background: #222;
    cursor: default;
    height: 300px;
    transform-style: preserve-3d;
    transition: transform 0.8s ease 0s;
    width: 100%;
    animation: giro 1s 1;
   
  }
  
  .card p {
    margin-bottom: 1.8em;
  }
  
  .card .front,
  .card .back {
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    color: white;
    display: block;
    font-size: 1.2em;
    height: 100%;
    padding: 0.8em;
    position: absolute;
    text-align: center;
    width: 100%;
    border-radius: 25px;
    overflow: hidden;
  }
  
  .card .front strong {
    background: #fff;
    border-radius: 100%;
    color: #222;
    font-size: 1.5em;
    line-height: 30px;
    padding: 0 7px 4px 6px;
  }
  
  .card .back {
    transform: rotateY( 180deg);
  }
  
  .card .back a {
    padding: 0.3em 0.5em;
    background: #333;
    color: #fff;
    text-decoration: none;
    border-radius: 1px;
    font-size: 0.9em;
    transition: all 0.2s ease 0s;
  }
  
  .card .back a:hover {
    background: #fff;
    color: #333;
    text-shadow: 0 0 1px #333;
  }
  
  .cardBox:nth-child(1) .card .back {
    background: rgb(204, 126, 66);
  }
  
  .cardBox:nth-child(2) .card .back {
    background: rgb(204, 126, 66);
  }
  
  .cardBox:nth-child(3) .card .back {
    background: rgb(204, 126, 66);
  }
  .cardBox:nth-child(4) .card .back {
    background: rgb(204, 126, 66);
  }
  
  .cardBox:nth-child(2) .card {
    animation: giro 1.5s 1;
  }
  
  .cardBox:nth-child(3) .card {
    animation: giro 2s 1;
  }
  
  .cardBox:nth-child(4) .card {
    animation: giro 2.5s 1;
  }
  
  @keyframes giro {
    from {
      transform: rotateY( 180deg);
    }
    to {
      transform: rotateY( 0deg);
    }
  }
  
  @media screen and (max-width: 767px) {
    .cardBox {
      margin-left: 2.8%;
      margin-top: 3%;
      width: 46%;
    }
    .card {
      height: 285px;
    }
    .cardBox:last-child {
      margin-bottom: 3%;
    }
  }
  
  @media screen and (max-width: 480px) {
    .cardBox {
      width: 94.5%;
    }
    .card {
      height: 260px;
    }
  }
.card .front img{
    height:100%;
    width:100%;
}

